@import url(https://fonts.googleapis.com/css?family=Roboto:300,400&display=swap&subset=latin-ext);
@-webkit-keyframes spinright{from{-webkit-transform:translate(-50%, -50%) scale(0.3) rotate(90deg);transform:translate(-50%, -50%) scale(0.3) rotate(90deg)}to{-webkit-transform:translate(-50%, -50%) scale(0.3) rotate(0deg);transform:translate(-50%, -50%) scale(0.3) rotate(0deg)}}@keyframes spinright{from{-webkit-transform:translate(-50%, -50%) scale(0.3) rotate(90deg);transform:translate(-50%, -50%) scale(0.3) rotate(90deg)}to{-webkit-transform:translate(-50%, -50%) scale(0.3) rotate(0deg);transform:translate(-50%, -50%) scale(0.3) rotate(0deg)}}@-webkit-keyframes spinleft{from{-webkit-transform:translate(-50%, -50%) scale(0.3) rotate(0deg);transform:translate(-50%, -50%) scale(0.3) rotate(0deg)}to{-webkit-transform:translate(-50%, -50%) scale(0.3) rotate(-90deg);transform:translate(-50%, -50%) scale(0.3) rotate(-90deg)}}@keyframes spinleft{from{-webkit-transform:translate(-50%, -50%) scale(0.3) rotate(0deg);transform:translate(-50%, -50%) scale(0.3) rotate(0deg)}to{-webkit-transform:translate(-50%, -50%) scale(0.3) rotate(-90deg);transform:translate(-50%, -50%) scale(0.3) rotate(-90deg)}}*{margin:0;padding:0;box-sizing:border-box}body{display:flex;justify-content:center;align-items:center;background-color:#f4cd8a;-webkit-font-smoothing:subpixel-antialiased;font-family:'Roboto', sans-serif;height:100vh}.card{position:relative;width:394px;min-height:716px;border-radius:22px;font-weight:300;padding:10px 20px;background:#7fadad;color:#fafafa;box-shadow:0px 0px 23px 3px rgba(0,0,0,0.21);overflow:hidden}.card .icon{position:absolute;top:-45%;left:50%;-webkit-transform:translate(-50%, -50%) scale(0.3);transform:translate(-50%, -50%) scale(0.3);-webkit-transform-origin:50% 1250px;transform-origin:50% 1250px}.card .rightSpin{-webkit-animation:spinright 1s ease .2s 1 normal both;animation:spinright 1s ease .2s 1 normal both}.card .leftSpin{-webkit-animation:spinleft 1s ease .2s 1 normal both;animation:spinleft 1s ease .2s 1 normal both}.card .result{margin-top:10%}.card .result p:nth-of-type(1){font-size:50px}.card .result p:first-letter{text-transform:uppercase}.card h1{margin-top:70px;text-align:center;text-transform:uppercase;font-size:50px;text-shadow:4px 3px 0 #7A7A7A}.card h2{margin-top:40px;text-align:center;text-transform:uppercase;font-size:40px;text-shadow:4px 3px 0 #7A7A7A}.card input{border:0px;background-color:transparent;position:absolute;z-index:2;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);bottom:120px;text-align:center;outline:none;color:#9c9c9c}.card button{text-align:center;display:inline-block;font-size:16px;padding:5px 5px;position:absolute;z-index:2;bottom:80px;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);border:none;border-radius:10px;letter-spacing:2px;color:rgba(255,255,255,0.9);background-color:#7fadad;width:200px;-webkit-transition:0.2s;transition:0.2s;outline:none}.card:after{content:'';display:block;position:absolute;bottom:0;left:50%;-webkit-transform:translate(-50%, 350px);transform:translate(-50%, 350px);box-sizing:border-box;width:600px;height:600px;border-radius:50%;background-color:rgba(255,255,255,0.9)}@media (max-width: 420px){body{background-color:#7fadad;position:fixed}.card{width:100vw;height:100vh;margin:0;border-radius:0px;padding-top:40px}.card:after{-webkit-transform:translate(-50%, 300px);transform:translate(-50%, 300px)}.card input{bottom:200px}.card button{bottom:150px}}@media (max-height: 650px){.card:after{-webkit-transform:translate(-50%, 330px);transform:translate(-50%, 330px)}}

body{
  font-size: 30px;
}
